import request from '@/utils/request'

// 获取未分组的学生
export function getUnsetStudents(gde, mjr) {
  return request({
    url: '/api/Student/get-unset',
    method: 'get',
    params: {
      gde,
      mjr
    }
  })
}

// 更新学生信息
export function updateStudentInfo(data) {
  return request({
    url: '/api/Student/update-info',
    method: 'post',
    data
  })
}

// 更新学生密码
export function updateStudentPassword(id, newPassword) {
  return request({
    url: '/api/Student/update-password',
    method: 'post',
    params: {
      id,
      newPassword
    }
  })
}

// 获取登录记录
export function getLoginRecord(account) {
  return request({
    url: '/api/Student/login-record',
    method: 'get',
    params: { account }
  })
}

// 获取所有学生
export function getAllStudents() {
  return request({
    url: '/api/Student',
    method: 'get'
  })
}

// 更新学生信息
export function updateStudentInfoByAdmin(id, data) {
  return request({
    url: `/api/Student/update/${id}`,
    method: 'post',
    data
  })
}

// 重置学生密码
export function resetStudentPassword(id, newPassword) {
  return request({
    url: '/api/Student/update-password',
    method: 'post',
    params: {
      id,
      newPassword
    }
  })
}

// 删除学生
export function deleteStudent(id) {
  return request({
    url: `/api/Student/delete/${id}`,
    method: 'post'
  })
}

// 添加单个学生
export function addStudent(data) {
  return request({
    url: '/api/Student',
    method: 'post',
    data
  })
}

// 批量添加学生
export function bulkCreateStudents(data) {
  return request({
    url: '/api/Student/bulk-create',
    method: 'post',
    data
  })
} 